<script>
    export default {
        name: "Info",
        data(){
            return {
                isShow:false,
                name:""
            }
        },
        created(){
            this.name = localStorage.getItem("usermsg");
        },
        methods:{
            tiankuang(){
                this.isShow = true;
            },
            change(){
                this.isShow = false;
            },
            deletename(){
                localStorage.removeItem('usermsg');
                this.$router.push("/profile");
            },
            updateUsername(){
                this.$router.push("/setusername");
            },
            updatePw(){
                this.$router.push("/forget");
            },
            updateAddress(){
                this.$router.push("/address");
